import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { AiFillLeftCircle, AiFillRightCircle } from 'react-icons/ai';
import { SiMinutemailer } from 'react-icons/si';
import MainStep from './steps/MainStep';
import BasicInfoStep from './steps/BasicInfoStep';
import AboutBusinessStep from './steps/AboutBusinessStep';
import FormSubmittedAlert from './FormSubmittedAlert';

export default function JoinBetaForm() {
  const [currentStep, setCurrentStep] = useState(1);
  const [formSubmitted, setFormSubmitted] = useState(false);
  const {
    register,
    handleSubmit,
    trigger,
    formState: { errors },
  } = useForm();

  const getStepPersentage = (stepNumber: number): string => {
    switch (stepNumber) {
      case 1:
        return `33%`;
      case 2:
        return `66%`;
      default:
        return `100%`;
    }
  };

  const removeEmptyValues = (data) => {
    const newData = {};
    Object.keys(data).forEach((key) => {
      if (data[key]) {
        newData[key] = data[key];
      }
    });
    return newData;
  };

  const onSubmit = async (data) => {
    await fetch(`${process.env.GATSBY_CONTACT_CREATE_API_URL}`, {
      method: `POST`,
      body: JSON.stringify(removeEmptyValues(data)),
      headers: {
        'Content-Type': `application/json`,
      },
    });
    setFormSubmitted(true);
  };

  const stepsComponents = [
    <MainStep
      step={1}
      currentStep={currentStep}
      register={register}
      errors={errors}
    />,
    <BasicInfoStep
      step={2}
      currentStep={currentStep}
      register={register}
      errors={errors}
    />,
    <AboutBusinessStep
      step={3}
      currentStep={currentStep}
      register={register}
      errors={errors}
    />,
  ];

  const totalSteps = stepsComponents.length;

  const isLastStep = currentStep === totalSteps;

  return (
    <form
      className="antialiased bg-white text-gray-900 px-6"
      id="joinBeta"
      name="joinBeta"
      data-netlify="true"
      onSubmit={handleSubmit(onSubmit)}
      noValidate
    >
      <div className="max-w-xl mx-auto py-12 divide-y md:max-w-4xl">
        <div className="py-1 mt-10">
          <h2 className="text-2xl font-bold">Start a Company</h2>
          <div className="mt-2 max-w-md">
            <div className="grid grid-cols-1 gap-6">
              {formSubmitted ? (
                <FormSubmittedAlert />
              ) : (
                <>
                  <p>
                    It will be completely FREE during the whole beta cycle.
                    Don&apos;t worry, we won&apos;t spam you nor share
                    information you provided in any way. You can opt out of our
                    beta communication any time you want, but we greatly
                    appreciate your trust on us.
                  </p>
                  {stepsComponents}
                </>
              )}
            </div>
            <div className="flex justify-between mt-10">
              <button
                type="button"
                className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-xl shadow-xl transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-110"
                style={{
                  display:
                    currentStep > 1 && !formSubmitted ? `inline` : `none`,
                }}
                onClick={() => setCurrentStep(currentStep - 1)}
              >
                <AiFillLeftCircle
                  style={{ display: `inline` }}
                  className="mr-2"
                />
                Back
              </button>
              <button
                type="submit"
                form="joinBeta"
                className="bg-slate-900 hover:bg-slate-700 text-white font-bold py-2 px-4 rounded-xl shadow-xl transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-110"
                style={{
                  display: isLastStep && !formSubmitted ? `inline` : `none`,
                }}
              >
                <SiMinutemailer
                  style={{ display: `inline` }}
                  className="mr-2"
                />
                Send
              </button>
              <button
                type="button"
                className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-xl shadow-xl transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-110"
                style={{
                  display:
                    currentStep >= 1 && !isLastStep && !formSubmitted
                      ? `inline`
                      : `none`,
                }}
                onClick={async () => {
                  const requiredSteps = {
                    1: [`email_address`, `solutions`],
                    2: [`first_name`],
                    3: [`privacy_policy`],
                  };
                  const isValid = await trigger(requiredSteps[currentStep]);
                  if (isValid) {
                    setCurrentStep(currentStep + 1);
                  }
                }}
              >
                Next
                <AiFillRightCircle
                  style={{ display: `inline` }}
                  className="ml-2"
                />
              </button>
            </div>
            {!formSubmitted ? (
              <div className="w-full bg-gray-200 h-1 mt-10">
                <div
                  className="bg-blue-600 h-1"
                  style={{ width: getStepPersentage(currentStep) }}
                />
                <p>Page {totalSteps} of 3</p>
              </div>
            ) : null}
          </div>
        </div>
      </div>
    </form>
  );
}
