import React from 'react';
import countryList from 'country-list';

interface BasicInfoStepProps {
  step: number;
  currentStep: number;
  register: CallableFunction;
  errors: Record<string, any>;
}

export default function BasicInfoStep({
  step,
  currentStep,
  register = () => ({}),
  errors,
}: BasicInfoStepProps) {
  const countriesOptions = countryList
    .getData()
    .map(({ code, name }) => <option key={code}>{name}</option>);
  return (
    <div style={{ display: step === currentStep ? `inline` : `none` }}>
      <label className="block" htmlFor="firstName">
        <span className="text-gray-700">What&apos;s your first name?</span>
        <span className="text-rose-500">*</span>
        <input
          type="text"
          id="firstName"
          name="first_name"
          className={`
                    mt-1
                    block
                    w-full
                    rounded-md
                    bg-gray-100
                    border-transparent
                    focus:border-gray-500 focus:bg-white focus:ring-0
                    ${errors?.first_name ? `border-rose-500` : ` `}
                  `}
          placeholder="e.g. Satoshi"
          {...register(`first_name`, { required: true })}
        />
        {errors?.first_name && (
          <span className="text-sm text-rose-500">
            Please fill out this field
          </span>
        )}
      </label>
      <label className="block mt-5" htmlFor="lastName">
        <span className="text-gray-700">And your last name</span>
        <input
          type="text"
          id="lastName"
          name="last_name"
          className="
                    mt-1
                    block
                    w-full
                    rounded-md
                    bg-gray-100
                    border-transparent
                    focus:border-gray-500 focus:bg-white focus:ring-0
                  "
          placeholder="e.g. Nakamoto"
          {...register(`last_name`)}
        />
      </label>
      <label className="block mt-5" htmlFor="countryOfResidence">
        <span className="text-gray-700">
          What&apos;s your country of residence?
        </span>
        <select
          placeholder="Choose"
          name="country_of_residence"
          id="countryOfResidence"
          className="
                    mt-1
                    block
                    w-full
                    rounded-md
                    bg-gray-100
                    border-transparent
                    focus:border-gray-500 focus:bg-white focus:ring-0
                  "
          {...register(`country_of_residence`)}
        >
          <option>Choose...</option>
          {countriesOptions}
        </select>
      </label>
    </div>
  );
}
